:root {
  --toggle-light-bg: hsl(230, 19%, 60%);
  --toggle-dark-bg: hsl(0, 0%, 100%);
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instgram-start: hsl(37, 97%, 70%);
  --instgram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --toggle-light-mode: hsl(230, 22%, 75%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card-bg);
  --card-hover: var(--light-card-hover);
  --card-text: var(--light-text1);
  --toggle-bg: var(--toggle-light-bg);
  --toggle-mode: var(--toggle-light-mode);
  --toggle-button: var(--light-bg);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(var(--dark-text2));
    --card-hover: var(--dark-card-hover);
    --card-bg: var(--dark-card-bg);
    --toggle-bg: var(--toggle-dark-bg);
    --toggle-button:var(--light-text2:);
    --toggle-mode: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card-bg);
  --card-hover: var(--light-card-hover);
  --card-text: var(--light-text1);
  --toggle-bg: var(--toggle-light-bg);
  --toggle-mode: var(--toggle-light-mode);
  --toggle-button: var(--light-bg);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(var(--dark-text2));
  --card-hover: var(--dark-card-hover);
  --card-bg: var(--dark-card-bg);
  --toggle-bg: var(--toggle-dark-bg);
  --toggle-button:var(--light-text2:);
  --toggle-mode: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 71.875em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: ) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header__toggle {
  margin-left: auto;
}

.cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.875rem;
}
@media (min-width: 48em) {
  .cards {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.card {
  background: var(--card-bg);
  color: var(--dark-text1);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  width: 15.9375rem;
  transition: all 150ms ease-in-out;
}
.card:hover {
  cursor: pointer;
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instgram {
  padding-top: calc(rem(25) + rem(5));
}
.card--instgram::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(235deg, var(--instgram-start), var(--instgram-end));
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color);
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
  font-weight: 700;
}
.card__count--small {
  font-size: 2rem;
  font-weight: 700;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  color: var(--light-text1);
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: 0;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle-bg);
}
.toggle label[for=dark] {
  height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  position: relative;
  pointer-events: auto;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 1px solid white;
}
.toggle__background {
  position: absolute;
  top: 0;
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 0.75rem;
  background: var(--toggle-mode);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 3px;
  left: calc(100% - 21px);
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.3125rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count,
.card-grid .card__change {
  align-self: end;
}
.card-grid img,
.card-grid .card__change {
  justify-self: end;
}