//640px ,1024px, 1400px
$breakpoints-up: (
  "meduim": "48em",
  "large": "71.875em",
  "xlarge": "87.5em",
);

$breakpoints-down: (
  "small": "39.9375em",
  "meduim": "71.875em",
  "large": "87.4375em",
);

@mixin breakpoint($size) {
  @media (min-width: map-get($breakpoints-up,$size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down,$size)) {
    @content;
  }
}
